.core .notesBox {
  padding: 0 0 40px; }
  .core .notesBox h3 {
    margin-bottom: 25px; }
    .core .notesBox h3 span {
      font-size: 14px;
      margin-left: 8px;
      opacity: 0;
      -webkit-transition: .2s;
      transition: .2s; }
      .core .notesBox h3 span:global(.show) {
        opacity: 1; }



